import { NextResponse } from 'next/server';
import { createClient } from '@/lib/supabase/server';

export async function GET() {
  try {
    const supabase = await createClient();
    
    // Usar fun√ß√£o SQL em vez de acessar a tabela diretamente
    const { data: icons, error } = await supabase
      .rpc('get_all_icons');

    if (error) {
      console.error('Error fetching icons:', error);
      // Se houver qualquer erro, retornar os √≠cones hardcoded
      return NextResponse.json(getHardcodedIcons());
    }

    // Se n√£o houver √≠cones, retornar os hardcoded
    if (!icons || icons.length === 0) {
      return NextResponse.json(getHardcodedIcons());
    }

    return NextResponse.json(icons);
  } catch (error) {
    console.error('Error in GET /api/icons:', error);
    return NextResponse.json(getHardcodedIcons());
  }
}

